body {
	margin-left: auto;
	margin-right: auto;
}

#layout-content {
	margin-left: 2em;
        max-width: 40em;
        text-align: justify;
}

div.infoblock {
	padding-top: .5em;
	border-top: 1px solid black;
	border-bottom: 1px solid black;
	background: #ffffee;
	padding-bottom: .5em;
}

div.recto, div.verso, div.exam {
	margin-top: .5em;
	margin-bottom: .5em;
	padding-top: .5em;
	padding-bottom: .5em;
}

div.verso {
	background: #E8E8E8;
}

div.exam {
	background: #E87878;
}

div.recto div.blocktitle, div.verso div.blocktitle, div.exam div.blocktitle {
	display: inline;
	font-weight: bold;
}

div.recto p, div.verso p, div.exam p {
	margin: 0em;
}

p:first-child {
	margin-top: 0em;
}

p:last-child {
	margin-bottom: 0em;
}

#toptitle {
	margin-bottom: 0.5em;
	font-weight: bold;
	font-size: 1.5em;
}

#title {
	margin-bottom: 0.5em;
}

h1 {
	font-size: 1.25em;
}

h2 {
	font-size: 1em;
}

div#footer {
	font-size: small;
	padding-top: 0.1em;
	margin-top: 2em;
	text-indent: 0em;
	color: #c0c0c0;
}

div#footer a {
	color: #80a0b0;
}

div#footer-text {
	float: right;
}
